import { Component, OnInit } from '@angular/core';



@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  value = {
    preMonth: null,
    presMonth: null,
    result: null
  };
  submitted = null;
  showForm = true;
  cost: number;
  charge : number;
  tbill: number;
  

  constructor() { }

  ngOnInit() {
  }
  onsubmit() {
    
    this.submitted = false;
    this.showForm= false;
    setTimeout(() => { this.submitted = true; this.showForm = true; }, 3000);
    this.value.result= this.value.presMonth - this.value.preMonth;
    if(this.value.result<=0){
      alert("The Present unit must be greater than previous month unit.");
      
    }
    else{
      if(this.value.result > 0 && this.value.result <= 50){
        this.cost = this.value.result * 1.45;
        this.charge = 25;
        this.tbill = Math.round(this.cost + this.charge);
      }
      else if(this.value.result > 50 && this.value.result <= 100){
        this.cost = this.value.result * 2.60;
        this.charge = 30;
        this.tbill = Math.round(this.cost + this.charge);
      }
      else if( this.value.result > 100 && this.value.result <= 200 ){
        this.cost = this.value.result * 3.25;
        this.charge = 50;
        this.tbill = Math.round(this.cost + this.charge);
      }
      else if( this.value.result > 200 && this.value.result <= 400 ){
        this.cost = this.value.result * 4.80;
        this.charge = 60;
        this.tbill = Math.round(this.cost + this.charge);
      }
      else if( this.value.result > 400 ){
        this.cost = this.value.result * 4.80;
        this.charge = 80;
        this.tbill = Math.round(this.cost + this.charge);
      }
    }
    console.log(this.value.result);
    
  }
  // tslint:disable-next-line:no-unused-expression
 

}
